import React from "react";
import styled from "@emotion/styled";
// Components
import Layout from "../components/Layout";
import Circle from "../components/Circle";
import ExternalLink from "../components/ExternalLink";
import ContentWrapper from "../components/ContentWrapper";
import IndustryBackgroundSection from "../components/BackgroundSection/IndustryBackgroundSection";
import AviationBackgroundSection from "../components/BackgroundSection/AviationBackgroundSection";
import CarsBackgroundSection from "../components/BackgroundSection/CarsBackgroundSection";
// Images
import downArrowSVG from "../../static/down-arrow.svg";
import linkSVG from "../../static/link.svg";
// Functions
import getRandomInt from "../utils/getRandomInt";
import scrollIntoView from "../utils/scrollIntoView";

const EmptySpace = styled.div`
  height: 60vh;
`;

const GraphText = styled.div`
  position: absolute;
  top: calc((60vh / 2) - (163px / 2));
  text-align: center;
  width: 100%;

  @media (min-width: 768px) {
    top: calc((60vh / 2) - (259px / 2));
  }
`;

const GraphMainText = styled.p`
  position: relative;
  color: yellow;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  font-weight: bold;
  font-family: Verdana;
  font-size: 4rem;

  margin: 1rem 0;

  @media (min-width: 768px) {
    margin: auto;
    font-size: 6rem;

    margin: 2rem 0;
  }
`;

const GraphSubText = styled.p`
  position: relative;
  color: white;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  font-weight: bold;
  font-family: Verdana;
  font-size: 1.5rem;
  margin: 0;

  @media (min-width: 768px) {
    font-size: 2rem;
  }
`;

const StartContent = styled.div`
  background-color: rgba(0, 0, 0, 0.54);
  padding: 1rem 16px 0 16px;
  height: calc(40vh - 1rem);

  @media (min-width: 768px) {
    padding: 2.5rem 0 0 16px;
    height: calc(40vh - 2.5rem);

    p {
      font-size: 1.5rem;
    }
  }

  p {
    margin-bottom: 0.5rem;

    @media (min-width: 768px) {
      text-align: center;
    }
  }
`;

const Countdown = styled.p`
  color: yellow;
  font-weight: bold;
  font-family: Verdana;
  font-size: 1.5rem;

  @media (min-width: 768px) {
    margin: auto;
    font-size: 2rem;
  }
`;

const ReadMore = styled.a`
  color: white;
  &:hover {
    text-decoration: none;
  }

  width: 100vw;
  position: absolute;
  bottom: 0;
  left: 0;
  margin-bottom: 5px;

  p {
    text-align: center;
  }

  img {
    color: white;
    display: block;
    width: 10%;
    margin: auto;

    @media (min-width: 768px) {
      width: 42px;
    }
  }
`;

const BigLink = styled.a`
  font-size: 1.5rem;
  font-weight: bold;

  img {
    height: 20px;
  }
`;

// GLOBAL CONSTANTS
const CURRENT_BUDGET = 370; // (MtCO2) post-2019
const CURRENT_EMISSIONS = 49.8; // (MtCO2) preliminary numbers for 2019
const START_DATE = new Date("Jan 1, 2020 00:00:00");

export default function Home() {
  const [counter, setCounter] = React.useState(0);
  const [timeLeft, setTimeLeft] = React.useState({});
  const [background, setBackground] = React.useState();

  React.useEffect(() => setBackground(getRandomInt(3)), []);

  // Initial calculations
  React.useEffect(() => {
    const millisecondsLeft = (CURRENT_BUDGET / CURRENT_EMISSIONS) * 31556952000;
    const then = START_DATE.getTime() + millisecondsLeft;
    const now = new Date().getTime();
    setCounter(then - now);
  }, []);

  // Updating counter
  React.useEffect(() => {
    setTimeLeft({
      years: Math.floor(counter / (1000 * 60 * 60 * 24 * 365)),
      days: Math.floor(
        (counter % (1000 * 60 * 60 * 24 * 365)) / (1000 * 60 * 60 * 24)
      ),
      hours: Math.floor((counter % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
      minutes: Math.floor((counter % (1000 * 60 * 60)) / (1000 * 60)),
      seconds: Math.floor((counter % (1000 * 60)) / 1000),
    });

    counter > 0 && setTimeout(() => setCounter(counter - 1000), 1000);
  }, [counter]);

  const startContent = (
    <>
      <EmptySpace>
        <Circle
          maxVal={12}
          progress={3.2}
          lineWidth={30}
          progressColor={"yellow"}
          responsive={true}
        />
        <GraphText>
          <GraphSubText>2018/2019</GraphSubText>
          <GraphMainText>-3,2 %</GraphMainText>
          <GraphSubText>(Krvs -12 %)</GraphSubText>
        </GraphText>
      </EmptySpace>
      <StartContent>
        <p>
          I <b>Sverige</b> mste utslppen sluta helt om:{" "}
        </p>
        <Countdown>{`${timeLeft.years} r, ${timeLeft.days} ${
          timeLeft.days === 1 ? "dag" : "dagar"
        }, ${timeLeft.hours} ${timeLeft.hours === 1 ? "timme" : "timmar"}, ${
          timeLeft.minutes
        } min, ${timeLeft.seconds} sek`}</Countdown>
        <p>om vi ska uppn vr del av Parisavtalet.</p>

        <ReadMore onClick={() => scrollIntoView("#FAQ-start")}>
          <p>Ls mer</p>
          <img src={downArrowSVG} alt="" />
        </ReadMore>
      </StartContent>
    </>
  );

  return (
    <Layout>
      {background === 1 ? (
        <IndustryBackgroundSection>{startContent}</IndustryBackgroundSection>
      ) : background === 2 ? (
        <AviationBackgroundSection>{startContent}</AviationBackgroundSection>
      ) : (
        <CarsBackgroundSection>{startContent}</CarsBackgroundSection>
      )}

      <section>
        <ContentWrapper>
          <h2 id="FAQ-start">Var kommer siffrorna ifrn?</h2>
          <p>
            Nedrkningen r baserad p Sveriges andel av den globala
            koldioxidbudgeten som vi kan slppa ut innan vi uppnr 1,5 graders
            uppvrmning. Tillsammans med siffror p Sveriges rliga utslpp kan
            man rkna fram hur mycket tid vi har kvar att bli fossilfria innan
            vi misslyckas med vr andel av 1,5 gradersmlet. Dock s rknar man
            inte in internationellt flyg eller sjfart. S nedrkningsklockan
            visar en verskattad tid vi har kvar.
          </p>
          <p>Kllor:</p>
          <ul>
            <li>
              <ExternalLink href="https://www.naturvardsverket.se/Sa-mar-miljon/Klimat-och-luft/Klimat/Tre-satt-att-berakna-klimatpaverkande-utslapp/Kvartals--och-preliminara-arsvisa-vaxthusgasutslapp/">
                <i>Kvartals- och preliminra rsvisa vxthusgasutslpp</i>
              </ExternalLink>
            </li>
            <li>
              <ExternalLink href="https://www.tandfonline.com/doi/full/10.1080/14693062.2020.1728209">
                <i>
                  A factor of two: how the mitigation plans of climate
                  progressive nations fall far short of Paris-compliant
                  pathways
                </i>
              </ExternalLink>
            </li>
          </ul>
        </ContentWrapper>
        <ContentWrapper>
          <h2>Vad r en koldioxidbudget?</h2>
          <p>
            Mngden koldioxid i atmosfren korrelerar med en viss genomsnittlig
            global temperatur. P s stt kan man rkna fram ett tak fr hur
            mycket koldioxid vi kan slppa ut innan vi nr en viss global
            uppvrmning.
          </p>
          <p>
            <ExternalLink href="https://en.wikipedia.org/wiki/Emissions_budget">
              Ls mer hr
            </ExternalLink>
          </p>
        </ContentWrapper>
        <ContentWrapper>
          <h2>Vad betyder cirkeln?</h2>
          <p>
            Den ifyllda delen av cirkeln representerar Sveriges senaste rliga
            utslppsminskning mot vad som krvs fr att klara Parisavtalet (hela
            cirkeln). Just nu behver Sverige minska utslppen med minst
            <b> 12 %</b> per r, men siffran blir tuffare fr varje r vi inte lyckas
            uppn det.
          </p>
        </ContentWrapper>
        <ContentWrapper>
          <h2>Vad hnder om vi inte hinner stoppa utslppen i tid?</h2>
          <p>
            Om Sverige har spenderat hela sin koldioxidbudget fr att n 1.5
            gradersmlet brjar vi att gnaga p andra lnders koldioxidbudgetar.
          </p>
        </ContentWrapper>
        <ContentWrapper>
          <h2>Det r ont om tid. r det inte krt?</h2>
          <p>
            Det r sant att det r ont om tid men det r inte omjligt att
            fortfarande klara mlet, men d krvs mer drastiska och ambitisa
            handlingar av oss alla. Som politiker, fretag eller individ. Ny
            teknik behvs men p den tidshorisont vi tittar p s kan vi inte
            frlita oss p att tekniken kommer rdda oss. Andra lsningar som
            kan minska utslppen nu behvs.
          </p>
        </ContentWrapper>
        <ContentWrapper>
          <h2>Vad kan JAG egentligen gra?</h2>
          <p>
            Du kan pverka mer n vad du tror. Frn att minska dina egna utslpp
            till att pverka och frndra normer. Hr kommer ngra frslag p
            hur:
          </p>
          <ul>
            <li>
              <ExternalLink href="https://www.wwf.se/klimat/det-har-kan-du-gora/">
                <i>Det hr kan du gra (Vrldsnaturfonden)</i>
              </ExternalLink>
            </li>
            <li>
              <ExternalLink href="https://www.naturskyddsforeningen.se/agera-for-klimatet">
                <i>10 saker du kan gra fr klimatet (Naturskyddsfreningen)</i>
              </ExternalLink>
            </li>
          </ul>
        </ContentWrapper>

        <ContentWrapper style={{ textAlign: "center" }}>
          <BigLink href="https://www.dn.se/klimatet-just-nu/">
            Klockan fr vrlden just nu <img src={linkSVG} alt="" />
          </BigLink>
        </ContentWrapper>
      </section>
    </Layout>
  );
}
